/*purgecss start ignore*/

.lightboxjs{
  margin:0;
  padding:0
}

.lightboxjs *{
  box-sizing:border-box
}

.lightboxjs img,.lightboxjs svg,.lightboxjs video{
  display:block;
  vertical-align:middle;
  height:auto;
  max-width:100%
}

.slideshowContainer{
  background-color:#000;
  height:100vh;
  width:100vw;
  position:fixed;
  top:0;
  left:0
}

.slideshowBtn{
  padding-bottom:2px
}

.closeIcon:focus,.closeIcon:hover,.icon:hover,.iconsHeader svg:focus,.iconsHeader svg:hover,.next1:hover,.prev1 span:hover{
  text-decoration:none;
  cursor:pointer;
  color:#e6e6e6
}

.nextIcon:hover,.prevIcon:hover{
  background-color:rgba(0,0,0,.8);
  color:#e6e6e6
}

.mainImg{
  -o-object-fit:contain;
  object-fit:contain;
  max-width:100%;
  max-height:100%;
  width:auto;
  height:auto
}

.show{
  display:flex
}

.displayHidden{
  display:none
}

.thumbnail{
  opacity:1;
  transition:all .3s;
  cursor:pointer;
  position:relative
}

img.hoverShadow{
  transition:.3s
}

.center_content{
  margin-left:auto;
  margin-right:auto
}

.hoverShadow:hover{
  box-shadow:0 4px 8px 0 rgba(0,0,0,.2),0 6px 20px 0 rgba(0,0,0,.19)
}

.next1{
  right:1%;
  padding-left:2vw;
  padding-right:3vw
}

.prev1{
  left:2%;
  padding-right:2vw
}

.thumbnails img{
  display:inline
}

.thumbnailsWithCaption{
  width:85%
}

@media screen and (min-width:20em){
  .imageSlide{
    height:67vw;
    width:60%
  }

  .thumbnailsOuterContainer{
    position:fixed;
    bottom:5%;
    width:100vw
  }

  .imgTitleContainer{
    padding-bottom:16px;
    width:100vw
  }

  .thumbnails{
    margin-left:auto;
    margin-right:auto
  }

  .thumbnails img{
    height:7vh
  }

  .next1{
    right:.9%
  }

  .prev1{
    left:1.5%
  }
}

.thumbnails img{
  height:5vh;
  width:50px;
  border-radius:6px;
  margin-right:7px;
  -o-object-fit:cover;
  object-fit:cover
}

.thumbnailsOuterContainer{
  bottom:10%;
  height:5vh;
  display:flex;
  flex-wrap:wrap;
  flex-direction:row
}

.caption,.thumbnailsOuterContainer{
  position:absolute;
  width:100vw;
  z-index:10000000;
  margin-top:10px
}

.caption{
  bottom:14%;
  margin-left:auto;
  margin-right:auto
}

.galleryImg{
  cursor:pointer;
  margin:.2em
}

.slideshowAnimContainer{
  width:99vw;
  height:100%;
  max-height:100%;
  overflow:hidden;
  touch-action:none;
  padding:0;
  overflow-y:auto;
  margin:0;
  display:flex;
  justify-content:center;
  align-items:center;
  position:fixed;
  top:0;
  left:0;
  z-index:1000000
}

.fullScreenContainer img{
  height:100vh;
  max-width:100%;
  max-height:100%
}

.imageInnerContainer img{
  height:80vh;
  max-width:100%;
  max-height:100%
}

.imageInnerContainer{
  position:fixed;
  display:flex;
  flex-direction:column;
  justify-content:center;
  top:10%;
  align-content:center;
  margin-left:auto;
  margin-right:auto;
  text-align:center;
  z-index:1000000
}

.magnifyWrapper{
  margin-bottom:3em;
  margin-left:auto;
  margin-right:auto
}

.slideshowInnerContainer{
  position:fixed;
  display:flex;
  flex-direction:column;
  justify-content:center;
  transition:top .2s ease-in-out 0s;
  top:10%;
  align-content:center;
  margin-left:auto;
  margin-right:auto;
  text-align:center;
  z-index:1000000
}

.slideshowInnerContainerThumbnails{
  top:12%
}

.fullScreenContainer{
  top:0
}

.lightboxContainer{
  width:100vw;
  height:100%;
  position:relative
}

.lightboxContainer,.next1,.prev1{
  display:flex;
  justify-content:center;
  align-items:center
}

.next1,.prev1{
  top:calc(50% - 20px);
  position:absolute;
  transition:all .2s;
  padding-bottom:2vh;
  padding-top:2vh;
  -webkit-user-select:none;
  -moz-user-select:none;
  user-select:none;
  cursor:pointer;
  font-weight:700;
  border-radius:30px;
  width:40px;
  height:40px;
  font-size:18px;
  z-index:10000000
}

.next1:hover,.prev1:hover{
  color:#e6e6e6
}

.slideshowAnimImg{
  position:absolute;
  max-width:100vw
}

.controls{
  align-items:center
}

.controls,.iconsHeader{
  display:flex;
  flex-direction:row
}

.iconsHeader{
  color:#fff;
  position:absolute;
  cursor:pointer;
  top:10px;
  right:45px;
  z-index:999999999
}

.lightbox .iconsHeader svg{
  transition:all .3s
}

.iconsHeader svg:active{
  transform:scale(.97)
}

.slidePane{
  display:flex;
  flex-direction:row;
  flex-wrap:wrap
}

.imgTitle{
  text-align:center;
  width:80%;
  margin-left:auto;
  margin-right:auto
}

.iconsHeader svg{
  margin-left:.6em;
  padding:.2em;
  transition:all .1s;
  line-height:16px;
  vertical-align:baseline
}

@media screen and (min-width:6em){
  .iconsHeader{
    top:3%
  }

  .slideshowAnimContainer{
    width:100vw
  }

  .iconsHeader svg{
    padding-right:.3em
  }

  .lightboxjs img{
    cursor:pointer
  }

  .thumbnails img{
    height:9vh;
    width:22vw
  }

  .next1{
    right:.5%
  }

  .next1,.prev1{
    top:calc(48% - 20px)
  }

  .slideshowInnerContainer img{
    max-height:100%
  }
}

@media screen and (min-width:39em){
  .slideshowAnimContainer{
    width:100vw
  }

  .slideshow_img{
    display:grid;
    width:100vw;
    place-items:start
  }

  .slideshowInnerContainer{
    width:100vw;
    height:95vh;
    position:absolute
  }

  .lightbox_img{
    height:70vh
  }

  .imgTitleContainer{
    padding-bottom:0
  }

  .next1{
    right:.7%
  }

  .fullScreenContainer{
    width:100vw;
    height:100vh
  }

  .fullScreenContainer img{
    height:100vh
  }

  .thumbnails{
    margin-top:.6em
  }

  .thumbnails img{
    height:8vh;
    width:80px
  }

  .prev1{
    left:2.7%;
    padding-right:2vw
  }
}

@media screen and (min-width:42em){
  .imageSlide{
    height:70vh;
    width:80vw
  }

  .slideshow_img_fullscreen{
    display:grid;
    width:92vw;
    place-items:start;
    margin-left:auto;
    margin-right:auto;
    scrollbar-gutter:stable both-edges
  }

  .slideshowInnerContainer img{
    margin-top:0
  }

  .fullScreenContainer img{
    width:100vw
  }

  .slideshowInnerContainerFullScreen{
    top:0
  }

  .fullScreenLightboxImg{
    margin-left:auto;
    margin-right:auto;
    height:100vh
  }

  .slideshowInnerContainerFullScreen{
    height:100vh;
    width:100vw;
    position:absolute
  }

  .slideshowInnerContainerFullScreen img{
    -o-object-fit:contain;
    object-fit:contain
  }

  .thumbnails{
    scrollbar-width:none;
    -ms-overflow-style:none;
    margin-top:1.1em
  }

  .thumbnails::-webkit-scrollbar{
    display:none
  }

  .opacity-fade{
    transition:opacity .3s;
    -webkit-transition:opacity .3s;
    opacity:1
  }

  .thumbnailsOuterContainer{
    margin-top:.1em;
    padding-top:.3em;
    height:5vh;
    bottom:11%
  }

  .thumbnails img{
    height:10vh
  }

  .slideshowAnimContainer{
    width:100vw
  }

  .slideshowInnerContainer{
    width:100vw;
    height:95vh
  }

  .fullScreenContainer{
    width:100vw;
    height:100vh
  }

  .prev1{
    left:2%;
    padding-right:2vw
  }

  .next1{
    right:.4%
  }
}

@media screen and (min-width:80em){
  .fullScreenContainer img{
    width:100vw
  }
}

.light_icon,.light_icon:hover,.light_icon svg,.light_icon svg:hover{
  background-color:#fff;
  color:#000
}

.dark_icon,.dark_icon svg{
  background-color:transparent;
  color:silver
}

.dark_icon:hover,.dark_icon svg:hover{
  color:hsla(0,0%,42%,.8)
}

.light_header_icon svg{
  background-color:#fff;
  color:#000
}

.light_header_icon svg:hover{
  background-color:#fff;
  color:#000;
  color:rgba(24,24,24,.8)
}

.dark_header_icon svg{
  background-color:transparent;
  color:#626b77
}

.dark_header_icon svg:hover{
  color:rgba(99,99,99,.8)
}

.magnifier-image{
  -o-object-fit:contain;
  object-fit:contain
}

.thumbnails .active{
  border:4px solid #6b85ce
}

.thumbnails{
  width:95%
}

@media screen and (max-width:29em){
  .fullScreenContainer img{
    -o-object-fit:cover;
    object-fit:cover
  }

  .thumbnails{
    width:85%
  }

  .iconsHeader{
    position:fixed;
    left:115px
  }

  .next1,.prev1{
    padding-bottom:2vh;
    padding-top:2vh
  }

  .next1{
    padding-left:4vw
  }

  .prev1{
    padding-right:4vw
  }
}

@media screen and (max-width:42em){
  .slideshow_img{
    display:flex;
    justify-content:center;
    align-items:center;
    text-align:center;
    width:100vw;
    height:70vh
  }

  .slideshow_img img{
    margin-left:auto;
    margin-right:auto
  }

  .slideshow_img_fullscreen{
    display:flex;
    justify-content:center;
    align-items:center;
    text-align:center;
    width:100vw;
    height:70vh
  }

  .slideshowInnerContainerFullScreen{
    position:fixed;
    display:flex;
    flex-direction:column;
    justify-content:center;
    transition:top .2s ease-in-out 0s;
    top:10%;
    align-content:center;
    margin-left:auto;
    margin-right:auto;
    text-align:center;
    z-index:1000000
  }
}

.fullScreenContainer img{
  -o-object-fit:cover;
  object-fit:cover
}

.thumbnails{
  text-align:center;
  white-space:nowrap
}

.magnifier-image,.react-transform-component{
  margin-left:auto;
  margin-right:auto
}

.loader{
  width:44px;
  height:44px;
  box-sizing:border-box;
  border-radius:50%;
  display:inline-block;
  position:fixed;
  top:50%;
  left:50%;
  animation:rotation 1s linear infinite
}

@keyframes rotation{
  0%{
    transform:rotate(0deg)
  }

  to{
    transform:rotate(1turn)
  }
}

.day_loader{
  border:5px solid #626b77;
  border-bottom-color:transparent
}

.night_loader{
  border:5px solid;
  border-color:#fff #fff transparent
}

.lightbox_img{
  max-height:100%;
  margin-left:auto;
  margin-right:auto
}

.closeIcon svg,.controls svg{
  box-sizing:border-box
}

.thumbnailsAndCaption{
  bottom:0;
  height:20vh;
  margin-top:0;
  width:100vw
}

.thumbnailsWithCaption{
  margin-top:0
}

.slideImageAndCaption{
  top:7%
}

.slideshowAnimContainer{
  -ms-overflow-style:none
}

.thumbnails::-webkit-scrollbar{
  display:none
}

.roundedBorder{
  border-radius:20px
}

.maxWidthFull{
  max-width:100%
}

.maxWidthWithoutMagnifier{
  max-width:80%
}

@media screen and (max-width:880px){
  .iconsHeader{
    position:fixed;
